export default{
	data(){
		return{
			pic: '/static/img/reward.jpg',
		}
	},
	methods: {
		//保存图片
		savePic(){
			uni.saveImageToPhotosAlbum({
				filePath: this.pic,
				success: (res)=>{
					showToast('保存成功');
				},
				fail: (e)=>{
					console.log(e);
				}
			});
		}
	}
}
